.page {
  max-width: 1512px;
  width: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  background-color: #140132;
  color: #FCFCFD;
  padding: 0;
  margin: 0 auto;
}

.section {
  margin: 0 80px;
  align-items: center;
}
.section__container {
  max-width: 1122px;
}
.section__text-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 24px;
}
.section__description {
  max-width: 546px;
  display: block;
  color: #FCFCFD;
}

.header {
  padding: 0 80px;
  margin: 0;
  position: relative;
}
.header__container {
  padding: 20px 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  max-width: 92px;
  width: 100%;
  object-fit: cover;
}
.header__nav {
  max-width: 575px;
  width: 100%;
}
@media only screen and (max-width: 1440px) {
  .header__nav {
    display: none;
  }
}
.header__list {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 0;
}
.header__link {
  text-decoration: none;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #5E636E;
  font-family: Montserrat, Helvetica, sans-serif;
  transition: 0.3s;
}
.header__link_type_active {
  color: #01FFC2;
  font-weight: 700;
}
.header__link:hover {
  color: #FCFCFD;
}
@media only screen and (max-width: 1440px) {
  .header__link {
    order: 1;
  }
}
.header__audit-by-img {
  max-width: 176px;
  width: 100%;
  padding: 0;
  margin: 0 20px 0 0;
}

.container__right-col {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 1440px) {
  .container {
    order: 2;
  }
}

.currency {
  display: flex;
}

.rate {
  display: flex;
  margin: 0 30px 0 0;
}
.rate__icon {
  width: 30px;
  height: 30px;
}
.rate__container {
  margin: 0 0 0 5px;
}
.rate__name {
  font-size: 8px;
  line-height: 10px;
  font-weight: 400;
  color: #E5F7FF;
  font-family: Montserrat, Helvetica, sans-serif;
  text-transform: uppercase;
  display: block;
}
.rate__value {
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
  color: #FFFFFF;
  font-family: Montserrat, Helvetica, sans-serif;
  position: relative;
}
.rate__value::after {
  content: "";
  display: block;
  position: absolute;
  right: -10px;
  top: 50%;
  transform: translateY(-50%);
  width: 7px;
  height: 5px;
  background-image: url(../../images/up-currency-arrow.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}

.lang-select {
  position: relative;
  width: 38px;
}
.lang-select__btn {
  width: 100%;
  color: #5E636E;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  font-family: Montserrat, Helvetica, sans-serif;
  background-color: transparent;
  padding: 0;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.lang-select__btn::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 5px;
  background-image: url(../../images/lang-arrow.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  pointer-events: none;
}

.lang {
  position: absolute;
  bottom: -20px;
  left: 0;
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  color: #5E636E;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  font-family: Montserrat, Helvetica, sans-serif;
}

.promo {
  background-image: url(../../images/background-promo.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  padding-bottom: 120px;
  height: 100vh;
}
.promo__container {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}
.promo__text-container {
  margin: auto 0;
}
.promo__left-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 400px;
  height: 100%;
}
.promo__title {
  color: #FCFCFD;
  font-size: 56px;
  line-height: 58px;
  font-weight: 900;
  font-family: Montserrat, Helvetica, sans-serif;
  display: block;
  max-width: 347px;
  margin: 0;
  padding: 0;
}
.promo__title_type_accent {
  color: #01FFC2;
}
.promo__subtitle {
  color: #FCFCFD;
  font-size: 28px;
  line-height: 38px;
  font-weight: 700;
  font-family: Montserrat, Helvetica, sans-serif;
}
.promo__link {
  text-decoration: none;
  color: #FCFCFD;
}
.promo__text {
  color: #ffffff;
  font-size: 20px;
  line-height: 28px;
  font-weight: 700;
  font-family: Montserrat, Helvetica, sans-serif;
  display: block;
}
.promo__app-img {
  width: 64px;
  height: 64px;
  object-fit: cover;
  padding: 0;
  margin: 0 0 10px;
}
.promo__list-app {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mission {
  display: flex;
  flex-direction: column;
  margin: 0 0 40px;
}
.mission__heading {
  color: #FCFCFD;
  font-size: 72px;
  line-height: 74px;
  font-weight: 900;
  font-family: Montserrat, Helvetica, sans-serif;
  margin: 0 0 40px;
  padding: 0;
}
.mission__heading_type_accent {
  color: #01FFC2;
}
.mission__description {
  font-size: 26px;
  line-height: 38px;
  font-weight: 400;
  font-family: Montserrat, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
}

.benefit {
  display: flex;
  flex-direction: column;
  margin: 0 0 99px;
}
.benefit__heading {
  color: #FCFCFD;
  font-size: 26px;
  line-height: 38px;
  font-weight: 700;
  font-family: Montserrat, Helvetica, sans-serif;
  margin: 0 0 40px;
  padding: 0;
}
.benefit__heading_type_accent {
  color: #01FFC2;
}
.benefit__description {
  font-size: 18px;
  line-height: 33px;
  font-weight: 400;
  font-family: Montserrat, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
}

.steps {
  display: flex;
  flex-direction: column;
  margin: 0 0 99px;
}
.steps__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 61px 101px;
  grid-template-columns: repeat(3, 1fr);
  background-image: url(../../images/steps-img.png);
  background-repeat: no-repeat;
  background-position: left bottom 69px;
  background-size: 723px;
}
.steps__item {
  display: flex;
  flex-direction: column;
  max-width: 348px;
}
.steps__item:nth-child(3) {
  background-image: url(../../images/coins.png);
  background-repeat: no-repeat;
  background-position: 95% 67%;
  background-size: 102px 97px;
}
.steps__item:nth-child(4) {
  grid-column: 3;
}
.steps__item:nth-child(5) {
  grid-row: 3;
  grid-column: 3;
}
.steps__number {
  color: rgba(113, 86, 253, 0.2);
  font-size: 100px;
  line-height: 80px;
  font-weight: 900;
  font-family: Montserrat, Helvetica, sans-serif;
}
.steps__description {
  color: #FCFCFD;
  font-size: 26px;
  line-height: 38px;
  font-weight: 700;
  font-family: Montserrat, Helvetica, sans-serif;
}
.steps__description_type_accent {
  color: #01FFC2;
}
.steps__subdescription {
  padding: 0;
  margin: 10px 0 0;
  max-width: 230px;
  color: #FCFCFD;
  font-size: 18px;
  line-height: 33px;
  font-weight: 400;
  font-family: Montserrat, Helvetica, sans-serif;
}

.box {
  display: flex;
  justify-content: center;
  margin: 0 0 99px;
}
.box__wrapper {
  display: flex;
}
.box__description {
  padding: 0;
  margin: 0;
  color: #FCFCFD;
  font-size: 72px;
  line-height: 74px;
  font-weight: 900;
  font-family: Montserrat, Helvetica, sans-serif;
  display: inline-block;
  max-width: 740px;
}
.box__description_type_accent {
  color: #01FFC2;
}
.box__subdescription {
  padding: 0;
  margin: 0;
  color: #FCFCFD;
  font-size: 18px;
  line-height: 33px;
  font-weight: 400;
  font-family: Montserrat, Helvetica, sans-serif;
  max-width: 458px;
}
.box__image {
  max-width: 423px;
  width: 100%;
  object-fit: cover;
}

.presale__border {
  background-image: url(../../images/border-image.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  position: relative;
}
.presale__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 118px;
  background-image: linear-gradient(rgba(113, 86, 251, 0.2), rgba(255, 13, 255, 0.2));
  border-radius: 40px;
}
.presale__text {
  color: #FCFCFD;
  font-size: 26px;
  line-height: 38px;
  font-weight: 700;
  font-family: Montserrat, Helvetica, sans-serif;
  padding: 0;
  margin: 0;
}
.presale__text_type_accent {
  color: #01FFC2;
}
.presale__btn {
  color: #FCFCFD;
  font-size: 26px;
  line-height: 38px;
  font-weight: 700;
  font-family: Montserrat, Helvetica, sans-serif;
  background-color: #7156FB;
  border-radius: 40px;
  border: none;
  outline: none;
  padding: 28px 40px;
  margin: 0;
  cursor: pointer;
}

.arrow-top {
  display: flex;
  position: absolute;
  top: -9px;
  left: 25.5%;
  width: 36px;
  height: 18px;
}
.arrow-top__first, .arrow-top__second, .arrow-top__third {
  background-image: url(../../images/arrow-animate.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  width: 12px;
  height: 18px;
}
.arrow-top__first {
  animation: arrow-first 1.7s linear infinite;
  animation-delay: 1.5s;
}
.arrow-top__second {
  animation: arrow-second 1.7s linear infinite;
  animation-delay: 1s;
}
.arrow-top__third {
  animation: arrow-third 1.7s linear infinite;
}
@keyframes arrow-first {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0;
  }
}
@keyframes arrow-second {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}
@keyframes arrow-third {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.arrow-bottom {
  display: flex;
  position: absolute;
  bottom: -9px;
  right: 25.5%;
  width: 36px;
  height: 18px;
  rotate: 180deg;
}
.arrow-bottom__first, .arrow-bottom__second, .arrow-bottom__third {
  background-image: url(../../images/arrow-animate.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  width: 12px;
  height: 18px;
}
.arrow-bottom__first {
  animation: arrow-first 1.7s linear infinite;
  animation-delay: 1.5s;
}
.arrow-bottom__second {
  animation: arrow-second 1.7s linear infinite;
  animation-delay: 1s;
}
.arrow-bottom__third {
  animation: arrow-third 1.7s linear infinite;
}
@keyframes arrow-first {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0;
  }
}
@keyframes arrow-second {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}
@keyframes arrow-third {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.product {
  height: 822px;
  display: flex;
  align-items: center;
  background-image: url(../../images/two-phones-img.png);
  background-size: 1088px;
  background-repeat: no-repeat;
  background-position: bottom right;
}
.product__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 0 0 195px;
  max-width: 344px;
}
.product__heading {
  color: #FCFCFD;
  font-size: 26px;
  line-height: 38px;
  font-weight: 700;
  font-family: Montserrat, Helvetica, sans-serif;
  padding: 0;
  margin: 0 0 20px;
}
.product__heading_type_accent {
  color: #01FFC2;
}
.product__descripton {
  font-size: 18px;
  line-height: 33px;
  font-weight: 400;
  font-family: Montserrat, Helvetica, sans-serif;
  padding: 0;
  margin: 0;
}

.menu {
  display: none;
  align-items: center;
  gap: 10px;
}
.menu__btn {
  width: 48px;
  height: 48px;
  border: none;
  outline: none;
  background: transparent;
  background-image: url(../../images/menu-btn.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
  cursor: pointer;
  position: relative;
}
.menu__btn-line::before, .menu__btn-line::after {
  content: "";
  transition: 0.2s;
  display: inline-block;
  position: absolute;
  background-color: #FCFCFD;
  height: 1px;
  width: 20px;
  top: 50%;
  left: 50%;
  margin-top: -1px;
  margin-left: -10px;
}
.menu__btn-line::before {
  transform: translateY(-5px);
}
.menu__btn-line::after {
  transform: translateY(5px);
}
.menu__btn-line_type_active::before {
  transform: rotate(-45deg);
}
.menu__btn-line_type_active::after {
  transform: rotate(45deg);
}
.menu__btn-text {
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #FCFCFD;
  font-family: Montserrat, Helvetica, sans-serif;
  text-transform: uppercase;
}
@media only screen and (max-width: 1440px) {
  .menu {
    display: flex;
    order: 0;
  }
}

.popup-menu {
  position: absolute;
  left: -90%;
  top: 101px;
  width: 100vw;
  height: 100vh;
  background-color: #140132;
  display: grid;
  grid-template-columns: 2fr 1.3fr;
  opacity: 0;
  transition: 0.8s;
}
.popup-menu_type_active {
  opacity: 1;
  left: 0;
}
.popup-menu__wrapper {
  grid-column: 2;
  margin-top: 100px;
}
.popup-menu__nav {
  position: relative;
}
.popup-menu__list {
  list-style: none;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 0;
  left: -100%;
  opacity: 0;
  transition: 0.8s;
  transition-delay: 0.4s;
}
.popup-menu__list-item {
  margin: 0 0 30px;
}
.popup-menu__list_type_active {
  left: 0;
  opacity: 1;
}
.popup-menu__link {
  color: #FCFCFD;
  text-decoration: none;
  font-size: 42px;
  line-height: 44px;
  font-weight: 700;
  font-family: Montserrat, Helvetica, sans-serif;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}
.popup-menu__link_type_active {
  color: #01FFC2;
}

.text {
  letter-spacing: -5px;
}
.text:hover .text__letter {
  display: inline-block;
  color: #01FFC2;
  animation: wiggleWiggle 0.3s ease-in-out;
}
.text:hover .text__letter:nth-child(2) {
  animation-delay: 0.15s;
}
.text:hover .text__letter:nth-child(3) {
  animation-delay: 0.2s;
}
.text:hover .text__letter:nth-child(4) {
  animation-delay: 0.25s;
}
.text:hover .text__letter:nth-child(5) {
  animation-delay: 0.3s;
}
.text:hover .text__letter:nth-child(6) {
  animation-delay: 0.35s;
}
.text:hover .text__letter:nth-child(7) {
  animation-delay: 0.4s;
}
.text:hover .text__letter:nth-child(8) {
  animation-delay: 0.45s;
}
.text:hover .text__letter:nth-child(9) {
  animation-delay: 0.5s;
}
.text:hover .text__letter:nth-child(10) {
  animation-delay: 0.55s;
}
.text:hover .text__letter:nth-child(11) {
  animation-delay: 0.6s;
}
.text:hover .text__letter:nth-child(12) {
  animation-delay: 0.65s;
}
.text:hover .text__letter:nth-child(13) {
  animation-delay: 0.7s;
}
@keyframes wiggleWiggle {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, -30px);
  }
  100% {
    transform: translate(0, 0);
  }
}